const memoryInfo = window.performance.memory;
// 
const resourceEntries = window.performance.getEntriesByType('resource');
// 
const bytesToGigaBytes = bytes => {
  return (bytes / (1024 * 1024 * 1024)).toFixed(2);
};
// 
const bytesToMegabytes = bytes => {
  return (bytes / (1024 * 1024)).toFixed(2);
};
console.log(':', bytesToGigaBytes(memoryInfo.jsHeapSizeLimit), 'GB');
console.log(':', bytesToGigaBytes(memoryInfo.totalJSHeapSize), 'GB');
console.log(':', bytesToGigaBytes(memoryInfo.usedJSHeapSize), 'GB');
