import "core-js/modules/es.array.push.js";
import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
import { EffectComposer } from "three/examples/jsm/postprocessing/EffectComposer.js";
import { RenderPass } from "three/examples/jsm/postprocessing/RenderPass.js";
import { OutlinePass } from "three/examples/jsm/postprocessing/OutlinePass.js";
import { CSS2DRenderer, CSS2DObject } from 'three/examples/jsm/renderers/CSS2DRenderer.js';
import TWEEN from "@tweenjs/tween.js";
// gltfGLTFLoader.js
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';
import { DRACOLoader } from 'three/examples/jsm/loaders/DRACOLoader';
import { ElNotification } from 'element-plus';
import { FORWARD } from "element-plus/es/components/virtual-list/src/defaults";
// import Stats from 'three/examples/jsm/libs/stats.module.js'

export default {
  data() {
    return {
      // 
      selectedObjects: [],
      mouse: new THREE.Vector2(),
      raycaster: new THREE.Raycaster(),
      // 
      ball: '',
      ball1: '',
      rod11: '',
      rods: [],
      left: 0,
      top: 0,
      visible: false,
      cvdBottomvisible: false,
      cvdCovervisible: false
    };
  },
  created() {},
  computed: {
    cvdName() {
      return this.$store.state.cvdName;
    }
  },
  watch: {
    cvdName(newVal, oldVal) {
      this.refresh();
    }
  },
  components: {},
  mounted() {
    this.init();
  },
  methods: {
    init() {
      this.initScene();
      this.initCamera();
      this.initLight();
      this.initRender();
      this.initModel();
      this.initControls(); //
      this.animate(); //
    },

    initScene() {
      this.scene = new THREE.Scene();
    },
    initCamera() {
      this.camera = new THREE.PerspectiveCamera(75, window.innerWidth * 0.2 / (window.innerHeight * 0.6), 0.1, 1000);
      this.camera.position.set(-15, 5, 4);
      this.scene.add(this.camera);
      // console.log(this.camera);
    },

    initLight() {
      this.light = new THREE.AmbientLight(0xffffff, 1);
      this.scene.add(this.light); //
    },

    initRender() {
      this.renderer = new THREE.WebGLRenderer({
        antialias: true,
        alpha: true,
        logarithmicDepthBuffer: true
      });
      this.renderer.setPixelRatio(window.devicePixelRatio);
      this.renderer.setSize(window.innerWidth * 0.4, window.innerHeight * 0.70); // 
      //webglcanvasbody 
      // this.renderer.outputEncoding = THREE.sRGBEncoding;
      document.getElementById("cvd").appendChild(this.renderer.domElement);
    },
    initModel() {
      const loader = new GLTFLoader(); //GLTF
      loader.load("model/CVD.glb", gltf => {
        let model = gltf.scene;
        model.scale.set(5, 5, 5);
        this.scene.add(model);
      });
    },
    initControls() {
      this.controls = new OrbitControls(this.camera, this.renderer.domElement);
      this.controls.update();
      this.controls.target = new THREE.Vector3(3.8, 6.08, -0.8);
    },
    animate() {
      // console.log(this.controls.target,this.camera);
      TWEEN.update();
      this.renderer.render(this.scene, this.camera);
      this.controls.update();
      if (this.composer) {
        this.composer.render();
      }
      this.rodsanimate();
      requestAnimationFrame(this.animate);
    },
    inFactory1() {
      this.ball = this.scene.getObjectByName('001_1');
      this.ball1 = this.scene.getObjectByName('001');
      for (let i = 0; i < 24; i++) {
        if (i == 0) {
          this.rods[0] = this.scene.getObjectByName('');
        } else if (i <= 9) {
          this.rods[i] = this.scene.getObjectByName(`00${i}`);
        } else {
          this.rods[i] = this.scene.getObjectByName(`0${i}`);
        }
      }
      // console.log(this.ball.position,this.ball1.position,this.bang11,this.scene);
      this.rod11 = this.scene.getObjectByName('011');
      this.ObjectTween(this.ball, {
        x: 0,
        y: 3,
        z: 0
      });
      this.ObjectTween(this.ball1, {
        x: 0,
        y: 3,
        z: 0
      });
    },
    ObjectTween(object, to) {
      var from = {
        x: object.position.x,
        y: object.position.y,
        z: object.position.z
      };
      var tween = new TWEEN.Tween(from).to(to, 100).easing(TWEEN.Easing.Quadratic.InOut);
      tween.onUpdate(() => {
        object.position.x = to.x;
        object.position.y = to.y;
        object.position.z = to.z;
      });
      tween.start();
    },
    onMouseClick(event) {
      const offsetHeight = event.srcElement.offsetHeight;
      const offsetWidth = event.srcElement.offsetWidth;
      //raycaster-11
      this.mouse.x = event.offsetX / offsetWidth * 2 - 1;
      this.mouse.y = -(event.offsetY / offsetHeight) * 2 + 1;
      // raycaster
      this.raycaster.setFromCamera(this.mouse, this.camera);
      // raycaster
      let intersects = this.raycaster.intersectObjects(this.scene.children);
      if (!intersects[0]) {
        this.visible = false;
        this.cvdBottomvisible = false;
        this.cvdCovervisible = false;
        this.selectedObjects = [];
        if (this.composer !== undefined) {
          this.composer.passes.pop();
        }
        return;
      } else {
        this.left = event.clientX - 120;
        this.top = event.clientY - 120;
        // console.log(intersects[0].object);
        if (intersects[0].object.name == '') {
          this.visible = false;
          this.cvdBottomvisible = true;
          this.cvdCovervisible = false;
          this.selectedObjects = [];
          this.selectedObjects.push(intersects[0].object);
          // this.selectedObjects.push(this.ball1)
          this.outlineObj(this.selectedObjects);
        } else if (intersects[0].object.name == '001_1') {
          this.visible = false;
          this.cvdBottomvisible = false;
          this.cvdCovervisible = true;
          this.selectedObjects = [];
          this.selectedObjects.push(intersects[0].object);
          this.outlineObj(this.selectedObjects);
        } else if (intersects[0].object.name == '001') {
          this.visible = false;
          this.cvdBottomvisible = false;
          this.cvdCovervisible = false;
          return;
        } else {
          this.selectedObjects = [];
          this.selectedObjects.push(intersects[0].object);
          // this.selectedObjects.push(this.ball1)
          this.outlineObj(this.selectedObjects);
          this.visible = true;
          this.cvdBottomvisible = false;
        }
      }
    },
    rodsanimate() {
      if (this.rods.length !== 0) {
        for (let i = 0; i < this.rods.length; i++) {
          // console.log(this.rods[i]);
          var x = this.rods[i].scale.x;
          var y = this.rods[i].scale.y;
          var z = this.rods[i].scale.z;
          this.rods[i].scale.set(x + 0.0001 > 0.1 ? x = 0.005 : x + 0.0001, y, z + 0.0001 > 0.1 ? z = 0.005 : z + 0.0001);
          // console.log(x,z);
        }
      }
    },

    //
    outlineObj(selectedObjects, color) {
      // EffectComposer
      this.composer = new EffectComposer(this.renderer);
      //   

      this.renderPass = new RenderPass(this.scene, this.camera);
      this.composer.addPass(this.renderPass);

      //ssaa
      // 

      this.outlinePass = new OutlinePass(new THREE.Vector2(window.innerWidth, window.innerHeight), this.scene, this.camera, selectedObjects);
      // this.outlinePass.renderToScreen = true
      this.outlinePass.edgeStrength = 12.0; // 
      this.outlinePass.edgeGlow = 1; // [0,1]   
      this.outlinePass.usePatternTexture = false; // 
      this.outlinePass.edgeThickness = 3; // 
      this.outlinePass.downSampleRatio = 1; // 
      this.outlinePass.pulsePeriod = 2; // 
      color == null ? this.outlinePass.visibleEdgeColor.set(parseInt(0x00f6ff)) : this.outlinePass.visibleEdgeColor.set(parseInt(color)); //  // 
      this.outlinePass.hiddenEdgeColor = new THREE.Color(0, 0, 0); // 
      // this.outlinePass.clear = true
      this.composer.addPass(this.outlinePass); // 
      this.outlinePass.selectedObjects = this.selectedObjects; // 
      // console.log(this.scene);
    },

    refresh() {
      this.ball = this.scene.getObjectByName('001_1');
      this.ball1 = this.scene.getObjectByName('001');
      this.camera.position.set(-15, 5, 4);
      this.ObjectTween(this.ball, {
        x: 0,
        y: 0,
        z: 0
      });
      this.ObjectTween(this.ball1, {
        x: 0,
        y: 0,
        z: 0
      });
      this.composer.passes.pop();
      // console.log(this.composer.passes);
    }
  }
};