import "core-js/modules/es.array.push.js";
import * as echarts from "echarts";
import { onMounted, onUnmounted, ref } from "vue";
import axios from '@/axios';
export default {
  setup() {
    // echart
    let echart = echarts;
    let resLength = 100;
    let inputdata = {};
    for (let i = 0; i < resLength; i++) {
      inputdata[`data${i}`] = [];
    }

    //  chart 
    let chart = null;

    // Echarts
    const initChart = () => {
      if (chart) {
        chart.dispose(); // 
      }

      chart = echart.init(document.getElementById("Trend"));
      // 
      chart.setOption({
        color: ["#70DBDB", "#B87333"],
        xAxis: {
          nameTextStyle: {
            fontSize: 10
          },
          type: "category",
          data: inputdata['data0'],
          axisLine: {
            lineStyle: {
              color: "#FFF",
              width: 1
            }
          },
          axisLabel: {
            textStyle: {
              color: "white"
            }
          }
        },
        yAxis: {
          nameTextStyle: {
            fontSize: 10
          },
          type: "value",
          axisLine: {
            lineStyle: {
              color: "#FFF",
              width: 1
            }
          },
          axisLabel: {
            textStyle: {
              color: "white"
            }
          },
          splitLine: {
            lineStyle: {
              color: "#2F2F4F"
            }
          }
        },
        series: [{
          name: '',
          data: inputdata['data1'],
          type: "line",
          label: {
            show: true,
            color: "white",
            position: "bottom"
          }
        }]
      });
      window.onresize = function () {
        // 
        chart.resize();
      };
    };

    // 
    const getcapacitygrowthtrendList = async () => {
      try {
        const res = await axios.get('/capacitygrowthtrend');
        const resLength = res.length; // 
        // res
        res.sort((a, b) => {
          // 
          const yearA = parseInt(a.year);
          const yearB = parseInt(b.year);

          // 
          return yearA - yearB;
        });
        for (let i = 0; i < resLength; i++) {
          let datalist = res[i];
          inputdata['data0'].push(datalist['year']);
          inputdata['data1'].push(datalist['capacity']);
        }
        // 
        initChart();
      } catch (error) {
        console.error(error);
      }
    };
    onMounted(() => {
      getcapacitygrowthtrendList();
    });
    onUnmounted(() => {
      if (chart) {
        chart.dispose(); // 
        chart = null; // 
      }
    });

    return {
      initChart
    };
  }
};